header{
    h1, a {
        font-size: 16px;
    }
    #header-row{
        display: flex;
        justify-content: space-between;
        max-width: 90%;
        margin: 0 auto;
        #h1-name{
            color: #383530;
            margin: 16px 0;
        }
        #desktop-nav{
            @media screen and (max-width: $sm){
                display: none;
            }
            #desktop-nav-ul{
                padding: 0;
            }
            li {
                margin-bottom: 10px;
                display: inline;
                list-style-type: none;
                margin-right: 4em;
                @media screen and (max-width: $md){
                    margin-right: 3em;
                }
                &:last-child{
                    margin-right: 0;
                }
            }
            .header-nav-link {
                display: inline-block;
                color: #000;
                text-decoration: none;
                color: white;
            }
            .header-nav-link::after {
                content: '';
                display: block;
                width: 0;
                height: 2px;
                background: white;
                transition: width .3s;
            }
            .header-nav-link:hover::after {
                width: 100%;
            }
        }
        #mobile-nav{
            @media screen and (min-width: $sm){
                display: none;
            }
        }
    }
    #banner-container{
        display: grid;
        grid-template-columns: repeat(48, 1fr);
        grid-template-rows: repeat(16, 1fr);
        height: 500px;
        position: relative;
        #orange-square{
            background: linear-gradient(67.5deg, #BD4A4E 30%, #DB5B3C 70%);
            border-radius: 5%;
            grid-column-start: 26;
            grid-column-end: 39;
            grid-row-start: 7;
            grid-row-end: -6;
            z-index: -1;
        }
        #welcome-text-section{
            // grid-column-start: 5;
            // grid-column-end: 20;
            // grid-row-start: 11;
            // grid-row-end: 13;
            position: absolute;
            top: 250px;
            left: 5%;
            #welcome-text-scene-1{
                font-size: 40px;
                background: linear-gradient(to right, #23074d , #BD4A4E );
                background-clip: border-box;
                -webkit-background-clip: text;
                -webkit-text-fill-color: transparent;
            }
        }
    }
    .downward-arrow{
        display: flex;
        justify-content: center;
        #dw-arrow{
            font-size: 50px;
            color: #BC4A4D;
            animation: bounce 3s infinite;
        }
    }

    #white-logo-container{
        grid-column-start: 23;
        grid-column-end: 44;
        grid-row-start: 6;
        grid-row-end: 3;
        #logo-white{
            max-width: 100%;
        }
    }
}


// KEYFRAMES

@keyframes bounce {
    0%{
        transform: translateY(0);
    }
    50%{
        transform:translateY(-20px);    
    }
}

